___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
}

body {
    font-family: 'Jost', 'Arial', sans-serif;
}

.fill {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}

.fill a {
    width: 50%;
    min-height: 100%
}

.headertext {
    font-size: 60px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}

.headertext_mobile {
    font-size: 45px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}

.subheadertext {
    font-size: 40px;
    font-weight: 200;
    margin: 0;
    padding: 0;
}

.subheadertext_mobile {
    font-size: 30px;
    font-weight: 200;
    margin: 0;
    padding: 0;
}

.quiztext {
    font-size: 40px;
    margin: 0;
    padding: 0;
}

.quiztext_mobile {
    font-size: 30px;
    margin: 0;
    padding: 0;
}

.value {
    font-size: 18px;
    font-weight: 500;
    margin: 0;
    padding: 0;
}

a {
    color: var(--blue-link);
}

/*
 * Taken from the example for footnote ref:
 * https://codesandbox.io/p/sandbox/react-a11y-footnotes-f9lpdy?file=%2Fsrc%2Fstyles.css%3A56%2C23
 * with some modifications
 */

/**
 * Initialiazing a \`footnotes\` counter on the wrapper
 */
body {
    counter-reset: footnotes;
}


/**
 * Inline footnotes references
 * 1. Increment the counter at each new reference
 * 2. Reset link styles to make it appear like regular text
 */
[data-a11y-footnotes-ref] {
    counter-increment: footnotes;
    /* 1 */
    text-decoration: none;
    /* 2 */
    color: var(--blue-link);
    /* 5 */
    cursor: default;
    /* 2 */
    outline: none;
    /* 2 */
}

/**
   * Actual numbered references
   * 1. Display the current state of the counter (e.g. \`[1]\`)
   * 2. Align text as superscript
   * 3. Make the number smaller (since it's superscript)
   * 4. Slightly offset the number from the text
   * 5. Reset link styles on the number to show it's usable
   */
[data-a11y-footnotes-ref]::after {
    content: "[" counter(footnotes) "]";
    /* 1 */
    vertical-align: super;
    /* 2 */
    font-size: 0.5em;
    /* 3 */
    margin-left: 2px;
    /* 4 */
    color: var(--blue-link);
    /* 5 */
    text-decoration: underline;
    /* 5 */
    cursor: pointer;
    /* 5 */
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;AAC1B;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB;AACJ;;AAEA;IACI,UAAU;IACV;AACJ;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;;;;EAIE;;AAEF;;EAEE;AACF;IACI,wBAAwB;AAC5B;;;AAGA;;;;EAIE;AACF;IACI,4BAA4B;IAC5B,MAAM;IACN,qBAAqB;IACrB,MAAM;IACN,uBAAuB;IACvB,MAAM;IACN,eAAe;IACf,MAAM;IACN,aAAa;IACb,MAAM;AACV;;AAEA;;;;;;;IAOI;AACJ;IACI,mCAAmC;IACnC,MAAM;IACN,qBAAqB;IACrB,MAAM;IACN,gBAAgB;IAChB,MAAM;IACN,gBAAgB;IAChB,MAAM;IACN,uBAAuB;IACvB,MAAM;IACN,0BAA0B;IAC1B,MAAM;IACN,eAAe;IACf,MAAM;AACV","sourcesContent":["* {\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: 'Jost', 'Arial', sans-serif;\n}\n\n.fill {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    overflow: hidden\n}\n\n.fill a {\n    width: 50%;\n    min-height: 100%\n}\n\n.headertext {\n    font-size: 60px;\n    font-weight: 500;\n    margin: 0;\n    padding: 0;\n}\n\n.headertext_mobile {\n    font-size: 45px;\n    font-weight: 500;\n    margin: 0;\n    padding: 0;\n}\n\n.subheadertext {\n    font-size: 40px;\n    font-weight: 200;\n    margin: 0;\n    padding: 0;\n}\n\n.subheadertext_mobile {\n    font-size: 30px;\n    font-weight: 200;\n    margin: 0;\n    padding: 0;\n}\n\n.quiztext {\n    font-size: 40px;\n    margin: 0;\n    padding: 0;\n}\n\n.quiztext_mobile {\n    font-size: 30px;\n    margin: 0;\n    padding: 0;\n}\n\n.value {\n    font-size: 18px;\n    font-weight: 500;\n    margin: 0;\n    padding: 0;\n}\n\na {\n    color: var(--blue-link);\n}\n\n/*\n * Taken from the example for footnote ref:\n * https://code