.sidebar-section-content {
    list-style-type: none;
    padding: 0;
    margin: 0;
    transition: max-height 0.25s, margin 0.25s, padding 0.25s;
    overflow-y: clip;
}

.sidebar-section-content.hidden, .sidebar-section-content.hidden .sidebar-section-content {
    transition: none
}

.sidebar-section-content .sidebar-section-content {
    margin-left: 0.75em;
    font-size: 15px;
}

.sidebar-section-content .sidebar-section-content_mobile {
    font-size: 25px;
}

.sidebar-section-content a {
    text-decoration: none;
}

.sidebar-section-content li {
    margin-bottom: 0.5em;
    position: relative;
}

.sidebar-section-content_mobile li {
    margin-bottom: 1rem;
}

.sidebar-section-title_mobile {
    margin-bottom: 0.75rem;
}

.sidebar_mobile {
    --mobile-sidebar-input-scale: 1.6;
}

.sidebar_mobile input {
    transform: scale(var(--mobile-sidebar-input-scale));
    transform-origin: left;
}

.sidebar_mobile button {
    transform: scale(var(--mobile-sidebar-input-scale));
}

.sidebar_mobile select {
    transform: scale(var(--mobile-sidebar-input-scale));
    transform-origin: left;
}

.sidebar_mobile label {
    padding-left: 0.5em;
}

.expandButton {
    padding: 0;
    border: none;
    background: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -18px;
    aspect-ratio: 1 / 1;
    transition: transform 0.25s;
    font-size: 12px;
    vertical-align: middle;
}

.expandButton:hover {
    background-color: transparent;
}

.sidebar-section-content .sidebar-section-content .expandButton {
    left: -16px;
}

.sidebar-section-content_mobile .expandButton {
    right: 0;
    left: unset;
    font-size: 27px;
}

.sidebar-section-content .sidebar-section-content.sidebar-section-content_mobile .expandButton {
    font-size: 25px;
    left: unset;
    right: 0px;
}`, "",{"version":3,"sources":["webpack://./src/components/sidebar.css"],"names":[],"mappings":";AACA;IACI,qBAAqB;IACrB,UAAU;IACV,SAAS;IACT,yDAAyD;IACzD,gBAAgB;AACpB;;AAEA;IACI;AACJ;;AAEA;IACI,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,mDAAmD;IACnD,sBAAsB;AAC1B;;AAEA;IACI,mDAAmD;AACvD;;AAEA;IACI,mDAAmD;IACnD,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;IAClB,MAAM;IACN,SAAS;IACT,WAAW;IACX,mBAAmB;IACnB,2BAA2B;IAC3B,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,QAAQ;IACR,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,UAAU;AACd","sourcesContent":["\n.sidebar-section-content {\n    list-style-type: none;\n    padding: 0;\n    margin: 0;\n    transition: max-height 0.25s, margin 0.25s, padding 0.25s;\n    overflow-y: clip;\n}\n\n.sidebar-section-content.hidden, .sidebar-section-content.hidden .sidebar-section-content {\n    transition: none\n}\n\n.sidebar-section-content .sidebar-section-content {\n    margin-left: 0.75em;\n    font-size: 15px;\n}\n\n.sidebar-section-content .sidebar-section-content_mobile {\n    font-size: 25px;\n}\n\n.sidebar-section-content a {\n    text-decoration: none;\n}\n\n.sidebar-section-content li {\n    margin-bottom: 0.5em;\n    position: relative;\n}\n\n.sidebar-section-content_mobile li {\n    margin-bottom: 1rem;\n}\n\n.sidebar-section-title_mobile {\n    margin-bottom: 0.75rem;\n}\n\n.sidebar_mobile {\n    --mobile-sidebar-input-scale: 1.6;\n}\n\n.sidebar_mobile input {\n    transform: scale(var(--mobile-sidebar-input-scale));\n    transform-origin: left;\n}\n\n.sidebar_mobile button {\n    transform: scale(var(--mobile-sidebar-input-scale));\n}\n\n.sidebar_mobile select {\n    transform: scale(var(--mobile-sidebar-input-scale));\n    transform-origin: left;\n}\n\n.sidebar_mobile label {\n    padding-left: 0.5em;\n